@import url(https://fonts.googleapis.com/css?family=Orbitron:400,800,900&display=swap);
body {
  margin: 0;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(2, 0, 36);
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(2, 0, 36, 1)),
    color-stop(16%, rgba(16, 16, 89, 1)),
    to(rgba(11, 177, 143, 1))
  );
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(16, 16, 89, 1) 16%,
    rgba(11, 177, 143, 1) 100%
  );
}

h1 {
  font-family: 'Orbitron', sans-serif;
  font-weight: 900;
  color: #0ccac4;
}

